import React, {useState} from 'react';
import connect from '@vkontakte/vk-connect';
import axios from "axios";
import View from '@vkontakte/vkui/dist/components/View/View';
import {ModalRoot, ModalPage, ModalPageHeader, HeaderButton, Placeholder, Button, Gallery, Switch} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon56ErrorOutline from '@vkontakte/icons/dist/56/error_outline';
import Icon56NotificationOutline from '@vkontakte/icons/dist/56/notification_outline';
import Icon56CheckCircleOutline from '@vkontakte/icons/dist/56/check_circle_outline';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';

import ButtonCart from  "./panels/catalog/ButtonInCart";


import {Div, Epic, Tabbar, TabbarItem, Panel, PanelHeader} from '@vkontakte/vkui';
//import Icon28Messages from '@vkontakte/icons/dist/28/messages';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon28Search from '@vkontakte/icons/dist/28/search';
import Icon28Notifications from '@vkontakte/icons/dist/28/notifications';
import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
// import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import AppHome from "./panels/Profile";
import CatalogProduct from "./panels/catalog/CatalogProduct";
import './panels/catalog/product.css';

import GetCartHome from "./panels/Cart/CartHome";

require('./panels/global/index.js');


class App extends React.Component {
    constructor (props) {
        super(props);

        this.state = {
            ParamStart:false,
            products:[],
            loadingCatalog:false,
            activeStory: 'catalog',
            loadingPage: true,
            user:[],
            userSystemInfo:{error:true,dataUser:""},
            cart:[],
            countCartProduct:'',
            addressDefault:null,
            AffiliateDefault:0,
            checkedTypeDilivery:0,
            commentary:"",
            typePay:1,
            countPerson:1,
            SalePoints:0,
            sumCart:0,
            activeModal:null,
            formNewAddressData:'',
            formEditAddressData:'',

            listAddressGlobal:[],
            listAffiliateGlobal:[],

            priceDelivery:0,
            minSumOrder:300,
            minSumFreeDelivery:0,

            //      
            errorOrderFinal:false,
            //     
            DeliveryPriceItog:false,

            //   ,  false,   
            DeliveryAddressOKCity:true,

            //,      
            addressOrderInfoUser:'',
            newOrderStats:false,

            StatusLoadSlide:false,
            slideHeaderArray:[],
            slideIndex: 0,

            StatusNotification:false,

            CityListBind:[]

        };
        this.onStoryChange = this.onStoryChange.bind(this);
    }

    getCityUser(){
        axios({
            method:'get',
            url:'https://sushi-time31.ru/api/getCity&apiKey='+global.ApiKey,
            responseType:'text'
        })
            .then(({data})=>{
                if("error" in data){

                }else{
                    this.state.ListCitySelect = data.list;
                    global.CityListBind = data.list;
                   // console.log(' ',global.CityListBind);
                }
            })
            .catch((err) =>{
                console.log('  ',err);
            });
    }

    parseQueryString = (string) => {
        return string.slice(1).split('&')
            .map((queryParam) => {
                let kvp = queryParam.split('=');
                return {key: kvp[0], value: kvp[1]}
            })
            .reduce((query, kvp) => {
                query[kvp.key] = kvp.value;
                return query
            }, {})
    };


    setOkNewOrderView(){
        console.log(' ');
        if(this.state.newOrderStats) {
            this.getCartDB(this.state.user.id);
            this.setState({activeStory: 'newOrderInfo'});
            this.getUserSystemInfo(this.state.user.id);
        }else{
            console.log('  ');
        }
    }


    getSuccesOrderPage(){
        return(
            <Placeholder stretched
                         icon={<Icon56CheckCircleOutline />}
                         action={<Button size="l" level="tertiary" onClick={()=>{this.setState({activeStory: 'catalog'});}}> </Button>}

            >
                <h2>  </h2>
                <p>      ,   .</p>
            </Placeholder>
        );
    }

    getDeliveryDataPriceItog(){
        if(this.state.minSumOrder < this.state.sumCart){
            this.setState({errorOrderFinal:false});
            if(this.state.minSumFreeDelivery <= this.state.sumCart){
                // ## 
                this.setState({DeliveryPriceItog:false});
            }else{
                //  
                this.setState({DeliveryPriceItog:true});
            }
        }else{
            this.setState({errorOrderFinal:true});
        }
        //console.log('  ',this.state.minSumFreeDelivery+' < '+this.state.sumCart);
    }


    getDeliveryDataPriceItogAffiliate(){
        this.setState({
            priceDelivery: 0,
            minSumOrder: 300,
            minSumFreeDelivery: 0
        });

        if(this.state.sumCart > 300){
            this.setState({errorOrderFinal:false});
            if(this.state.sumCart > 0){
                // ## 
                this.setState({DeliveryPriceItog:false});
            }else{
                //  
                this.setState({DeliveryPriceItog:true});
            }
        }else{
            this.setState({errorOrderFinal:true});
        }
        //console.log('   ');
    }

    onStoryChange (e) {
        this.setState({ activeStory: e.currentTarget.dataset.story })
    }

    setcountCartProduct(e){
        if(e == 0){e = '';}
        this.setState({
            countCartProduct:e
        });

        var sumCartItog = 0;
        this.state.cart.map((CPr)=>{
                sumCartItog +=CPr.price*CPr.count
            }
        );
        this.setState({sumCart:sumCartItog});

        document.cookie = "cart = "+this.state.cart;
    }

    setComment(e){
        this.setState({
            commentary:e
        });
    }

    setUser(e){
        this.setState({user:e});
    }

    setUserSystem(e){
        this.setState({userSystemInfo:{error:false,dataUser:e}});
    }

    getTypeSlideGet(){
        switch(this.state.ParamStart.vk_platform){
            case"desktop_web": return "desktop";break;
            default: return "mobile";
        }
    }


    getSlideHeaderArray(){
        if(this.state.ParamStart === false){

        }else {
            //console.log(this.state.ParamStart);
            axios('https://sushi-time31.ru/api/getSlideHeader&apiKey='+global.ApiKey,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                    data: {
                        typeSlide: this.getTypeSlideGet(),
                    },
                    responseType: 'text'
                })
                .then(({data}) => {
                    if ("error" in data) {
                        this.setSlideArray([], false);
                        console.log(data.error, '|', data.errorInfo);
                    } else {
                        //console.log(data.listSlide);
                        this.setSlideArray(data.listSlide, true);
                    }
                })
                .catch(({err}) => {
                    console.log("ERROR: --- ---  ", err);
                });
        }
    }

    setSlideArray(ArraySlide,status){
        if(status === true) {
            this.setState({slideHeaderArray: ArraySlide});
            if (this.state.StatusLoadSlide === false) {
                this.setState({StatusLoadSlide: true});
            }
        }else{
            if (this.state.StatusLoadSlide === true) {
                this.setState({StatusLoadSlide: false});
            }
        }
    }

    getSliderGalery(){
        var ArraySlides = Object.keys(this.state.slideHeaderArray);
        var Bullets = "dark";
        var AutoPlays = "7000";
        var countNumber = ArraySlides.length;
        if(ArraySlides.length <= 1){Bullets = false;AutoPlays = false;}
        if(this.state.StatusLoadSlide === true & ArraySlides.length > 0) {
            var SliderVisual = ArraySlides.map((datas,keys) => {
                //console.log(datas);
                return (<img key={keys} src={this.state.slideHeaderArray[datas].img} style={{maxWidth:"100%"}}/>);
            });

            return (
                <Gallery
                    autoplay={AutoPlays}
                    //slideIndex={this.state.slideIndex}
                    //onEnd={()=>{this.setState({"slideIndex":0})}}
                    slideWidth="100%"
                    style={{width:"100%",height: "auto"}}
                    bullets={Bullets}
                >
                    {SliderVisual}
                </Gallery>
            );
        }else{
            return(
                ""
            );
        }
    }


    reloadSlideHeaderSetTime(){
        this.getSlideHeaderArray();
        setTimeout(this.reloadSlideHeaderSetTime.bind(this),5000);
    }




    /////// PUSH

    getStatusNotificate(){
        connect.sendPromise("VKWebAppCallAPIMethod", {"method": "apps.isNotificationsAllowed", "params": {"user_id": "328965469","v":"5.103","access_token":"a07170c1a07170c1a07170c1c4a01c7753aa071a07170c1fdf9a8b9585d7876a020ecbf"}})
            .then((datas)=> {
                this.setState({StatusNotification: datas.response.is_allowed});
            })
            .catch((err)=>{
                this.setState({StatusNotification: false});
            });
    }

    setStatusNotification(e){
        this.setState({StatusNotification:e});
    }


    getNotificationPage(){
        if(this.state.StatusNotification === true){
            return(
                <Placeholder stretched
                             icon={<Icon56NotificationOutline />}
                             action={<Button size="l" level="tertiary" onClick={()=>{this.setState({activeStory: 'catalog'});}}> </Button>}

                >
                    <h2> </h2>
                    <p>     ,   !</p>
                </Placeholder>
            );
        }else{
            return(
                <Placeholder stretched
                             icon={<Icon56NotificationOutline />}
                             action={<Button size="l" level="tertiary"
                                             onClick={()=>{this.PushClick()}}
                             > </Button>}
                             style={{textAlign:"center"}}
                >
                      ,   
                </Placeholder>
            );
        }
    }

    PushClick(){
        if(this.state.StatusNotification === true ){
            this.OffPushNotyfication();
        }else{
            this.OnPushNotyfication();
        }
    }

    OffPushNotyfication(){
        connect.sendPromise("VKWebAppDenyNotifications", {})
            .then((datas)=>{
                this.setState({StatusNotification: false});
            })
            .catch((err)=> {
                    this.setState({StatusNotification: false});
                }
            );
    }

    OnPushNotyfication(){
        connect.sendPromise("VKWebAppAllowNotifications", {})
            .then((datas)=>{
                this.getStatusNotificate();
            })
            .catch((err)=> {
                    this.getStatusNotificate();
                }
            );
    }

    ///////

    componentDidMount() {
        this.getCityUser();
        this.getStatusNotificate();

        async function fetchData(setUser,getCartDB,getUserSystemInfo) {
            const user = await connect.sendPromise('VKWebAppGetUserInfo');
            setUser(user);
            //setPopout(null);
            //setModal(MODAL_CARD_MONEY_SEND);
            //console.log('...','  ',user.id);
            getCartDB(user.id);
            getUserSystemInfo(user.id);
        }

        axios({
            method:'get',
            // url:'https://sushi-time31.ru/test.json',
            // url:'https://sushi-time31.ru/test2.json',
            url:'https://sushi-time31.ru/api/Catalog&apiKey='+global.ApiKey+'&idCategoryKeyArray=true&idProductKeyArray=true',
            responseType:'text'
        }).then(({ data }) => {
            //console.log("  ",data.list);
            this.setState({
                // products: data.list[0].ProductList
                products: data.list,
                loadingCatalog:true
            });
        })
            .catch((err) => {
                console.log("ERROR: ------  ",err);
            });


        fetchData(this.setUser.bind(this),this.getCartDB.bind(this),this.getUserSystemInfo.bind(this));
        this.reloadSlideHeaderSetTime();
            setTimeout(function() {this.getIntervalCart(1)}.bind(this), 7000);
    }

    getIntervalCart(i){
       // console.log('   '+i);
        i++;
        this.getCartDB(this.state.user.id);
        setTimeout(function() {this.getIntervalCart(i)}.bind(this), 3000);
    }

    StatusReloadBasketData(dataOld,dataNew){
        if(dataOld === dataNew){return false;}else{return true;}
    }


    ReloadBasketDataFN(ListProduct){
       // console.log(' ');
        this.setState({
            // products: data.list[0].ProductList
            cart: ListProduct,
            loadingPage: false
        });
        this.setCountCartVisal();
    }

    getCartDB(id_user){
        if(this.state.loading === false) {
            this.setState({loading: true});
        }
        axios({
            method:'get',
            url:'https://sushi-time31.ru/api/getBasketVK&apiKey='+global.ApiKey+'&updateBasket=1minute&id_user_vk='+id_user,
            responseType:'text'
        }).then(({ data }) => {
            //console.log("  ",data);
            if("error" in data){
                data.listProduct = [];
                console.log(data.error,'|',data.errorInfo);
            }

            if(data.listProduct === null){
                data.listProduct = [];
            }

            var ListOld = JSON.stringify(this.state.cart);
            var ListNew = JSON.stringify(data.listProduct);

            var statusBasket = this.StatusReloadBasketData(ListOld,ListNew);

            if(statusBasket === true) {
                this.ReloadBasketDataFN(data.listProduct);
            }


        })
            .catch(({err}) => {
                console.log("ERROR: ------  ",err);
            });

    }


    getUserSystemInfo(id_user){
        this.setState({loading: true});

        axios({
            method:'get',
            url:'https://sushi-time31.ru/api/Users&apiKey='+global.ApiKey+'&updateUserSystemInfo=one&idUserVK='+id_user,
            responseType:'text'
        }).then(({ data }) => {
            //console.log("  ",data);
            if("error" in data){

                console.log(data.error,'|',data.errorInfo);
            }else{
                this.setUserSystem(data.list[0]);
            }

            this.setState({
                loadingPage: false
            });
            //console.log("userSystemInfo",this.state.userSystemInfo);
        })
            .catch(({err}) => {
                console.log("ERROR: ------  ",err);
            });
        //setUserSystem(e);
    }


    setCartServer(id_user,JsonBasket){
        //console.log(JsonBasket,'_________');
        axios('https://sushi-time31.ru/api/setBasketVK&apiKey='+global.ApiKey+'&id_user_vk='+id_user,
            {
            method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
            data: {
                basketJson: JsonBasket,
            },
            responseType:'text'
        })
            .then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo);
                }
            })
            .catch(({err}) => {
                console.log("ERROR: --- ---  ",err);
            });
    }

    setCart(e,tE = '+'){
        //console.log('  ,   ',e);
        //this.setState({cart:e});
        const counts = this.state.cart.length;
        if(tE == '+'){
            // 
            if(counts<=0) {
               // console.log('    ');
                this.state.cart.push(e);
                const countPlus = counts;
                this.state.cart[countPlus].count = 1;
                this.setcountCartProduct(1);
            }else{
               // console.log('  ,    ', counts);
                var noAdd = true;

                this.state.cart.map((cartProducts,key)=>
                    {if(cartProducts.id_Product === e.id_Product && cartProducts.type_product===e.type_product){
                       // console.log('    ');
                        this.state.cart[key].count = this.state.cart[key].count + 1;
                        setcountCartProductVar = setcountCartProductVar+this.state.cart[key].count;
                        noAdd =false;
                    }
                    }
                );

                if(noAdd === true){
                   // console.log('  ,     ');
                    this.state.cart.push(e);
                    const countPlus = counts;
                    this.state.cart[countPlus].count = 1;
                    setcountCartProductVar++;
                }

            }
        }else{
            // 
            if(counts<=0) {

            }else{
                var noAdd = true;
                this.state.cart.map((cartProducts,key)=>
                    {if(cartProducts.id_Product === e.id_Product && cartProducts.type_product===e.type_product){
                       // console.log('  1    id  ');
                        if( this.state.cart[key].count<=1){
                            // delete this.state.cart[key];
                            this.state.cart.splice(key, 1);
                        }else{
                            this.state.cart[key].count = this.state.cart[key].count - 1;
                            setcountCartProductVar = setcountCartProductVar+this.state.cart[key].count;
                            noAdd =false;
                        }
                    }
                    }
                );
            }

        }

        var setcountCartProductVar = 0;
        this.state.cart.map((CPr)=>
        {setcountCartProductVar = setcountCartProductVar+CPr.count}
        );
        this.setcountCartProduct(setcountCartProductVar);
        var setDataServer = btoa(encodeURI(JSON.stringify(this.state.cart)));
       // console.log('    ',setDataServer);
        this.setCartServer(this.state.user.id,setDataServer);
    }


    setCountCartVisal(){
       // this.getCartDB(this.state.user.id);
        var setcountCartProductVar = 0;
        this.state.cart.map((CPr)=>
            {setcountCartProductVar = setcountCartProductVar+CPr.count;
            }
        );
        this.setcountCartProduct(setcountCartProductVar);
    }

    setTypeDilivery(e){
        this.setState({checkedTypeDilivery:e});
       // console.log('    ',e);
    }

    setAddressDefault(e){
        if(e === 'del'){
            e = 0;
        }
        this.setState({addressDefault:e});
        //console.log('    ',e);
        //    3   
    }

    setAffiliateDefault(e){
        this.state.AffiliateDefault = e;
       // console.log('    ',e);
    }

    setModal(e){
        this.setState({activeModal:e});
    }

    render () {
        const queryParams = this.parseQueryString(window.location.search);
        const hashParams = this.parseQueryString(window.location.hash);

        if(this.state.ParamStart === false) {
            //console.log("State = false,  ", queryParams);
        }

        if(this.state.ParamStart === false) {
            this.state.ParamStart = queryParams;
        }

        // this.setState({"ParamStart":this.parseQueryString(window.location.search)});
        // console.log('  ',this.state.ParamStart,'  URL  ',window.location.search);

        const modal = (
            <ModalRoot activeModal={this.state.activeModal}>
                <ModalPage
                    id="MODAL_NEWADDRESS"
                    onClose={() => this.setModal(null)}
                    header={
                        <ModalPageHeader
                            right={<HeaderButton onClick={()=> {this.setModal(null)}}></HeaderButton>}
                        >
                             
                        </ModalPageHeader>
                    }
                    //icon={<Icon56MoneyTransferOutline />}
                    title=" "
                    dynamicContentHeight="true"
                    caption=""
                    actions={[{
                        title: '',
                        type: 'primary',
                        action: () => {
                            this.setModal(null);
                        }
                    }]}
                >
                    {this.state.formNewAddressData}
                </ModalPage>

                <ModalPage
                    id="MODAL_EditADDRESS"
                    onClose={() => this.setModal(null)}
                    header={
                        <ModalPageHeader
                            right={<HeaderButton onClick={()=> {this.setModal(null)}}></HeaderButton>}
                        >
                             
                        </ModalPageHeader>
                    }
                    //icon={<Icon56MoneyTransferOutline />}
                    title=" "
                    dynamicContentHeight="true"
                    caption=""
                    actions={[{
                        title: '',
                        type: 'primary',
                        action: () => {
                            this.setModal(null);
                        }
                    }]}
                >
                    {this.state.formEditAddressData}
                </ModalPage>
            </ModalRoot>
        );

        return (
            <Epic activeStory={this.state.activeStory} tabbar={
                <Tabbar>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'catalog'}
                        data-story="catalog"
                        text=""
                    ><Icon24Note /></TabbarItem>
                    <TabbarItem style={{"display":"none"}}
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'discover'}
                        data-story="discover"
                        text=""
                    ><Icon28Search /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'cart'}
                        data-story="cart"
                        label={this.state.countCartProduct}
                        text=""
                    ><Icon28MarketOutline /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'notifications'}
                        data-story="notifications"
                        text=""
                    ><Icon28Notifications /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'user'}
                        data-story="user"
                        text=""
                    ><Icon28User /></TabbarItem>
                </Tabbar>
            }>
                <View id="newOrderInfo" activePanel="newOrders" header={false}>
                    <Panel id="newOrders">
                        {/*<PanelHeader noShadow={true}> </PanelHeader>*/}
                        {this.getSuccesOrderPage()}
                    </Panel>
                </View>
                <View id="catalog" activePanel="catalog" header={false}>
                    <Panel id="catalog">
                        {/*<PanelHeader noShadow={true}> </PanelHeader>*/}
                        {
                            this.state.loadingCatalog
                                ?
                                <CatalogProduct setCart={this.setCart.bind(this)} CartProducts={this.state.cart} ProductsLists={this.state.products} sliderHeader={this.getSliderGalery.bind(this)} queryParams={queryParams}/>
                                :
                                <ScreenSpinner />
                        }
                    </Panel>
                </View>
                <View id="discover" activePanel="discover">
                    <Panel id="discover">
                        <PanelHeader> </PanelHeader>
                    </Panel>
                </View>
                <View id="cart" activePanel="cart" modal={modal}>
                    <Panel id="cart" style={{"paddingBottom":"40px"}}>
                        <PanelHeader theme="light"
                                     noShadow={false}
                        >
                            
                        </PanelHeader>
                        <div key="Cart_List" style={{"paddingBottom":"40px"}}>
                            {/*this.getCartVisual(this.state.cart,this.setCart.bind(this))*/}
                            <GetCartHome
                                ProductList={this.state.cart}
                                setCart={this.setCart.bind(this)}
                                userSystemInfo={this.state.userSystemInfo}
                                userInfo={this.state.user}
                                getUserSystemInfo={this.getUserSystemInfo.bind(this)}

                                addressDefault={this.state.addressDefault}
                                setAddressDefault={this.setAddressDefault.bind(this)}

                                AffiliateDefault={this.state.AffiliateDefault}
                                setAffiliateDefault={this.setAffiliateDefault.bind(this)}

                                checkedTypeDilivery={this.state.checkedTypeDilivery}
                                setTypeDilivery={this.setTypeDilivery.bind(this)}

                                commentary={this.state.commentary}
                                setComment={this.setComment.bind(this)}

                                countPerson={this.state.countPerson}

                                SalePoints={this.state.SalePoints}

                                setStates={this.setState.bind(this)}
                                setDeliveryDataItogs={this.getDeliveryDataPriceItog.bind(this)}
                                getDeliveryDataPriceItogAffiliate={this.getDeliveryDataPriceItogAffiliate.bind(this)}

                                sumCart={this.state.sumCart}

                                showModal={this.setModal.bind(this)}
                                hideModals={this.setModal.bind(this,null)}

                                DataDeliveryInfoPay={
                                    {
                                        priceDelivery:this.state.priceDelivery,
                                        minSumOrder:this.state.minSumOrder,
                                        minSumFreeDelivery:this.state.minSumFreeDelivery,

                                        //      
                                        errorOrderFinal:this.state.errorOrderFinal,
                                        //     
                                        DeliveryPriceItog:this.state.DeliveryPriceItog,

                                        //   ,  false,   
                                        DeliveryAddressOKCity:this.state.DeliveryAddressOKCity,

                                        addressOrderInfoUser:this.state.addressOrderInfoUser,

                                        checkedTypeDilivery:this.state.checkedTypeDilivery,
                                        AffiliateDefault:this.state.AffiliateDefault,
                                        addressDefault:this.state.addressDefault,

                                        listAddressGlobal:this.state.listAddressGlobal,
                                        listAffiliateGlobal: this.state.listAffiliateGlobal,

                                        comments:this.state.commentary,
                                        typePay:this.state.typePay,
                                        countPerson:this.state.countPerson,
                                        cart:this.state.cart

                                    }
                                }

                                setOkNewOrderView={this.setOkNewOrderView.bind(this)}

                            />

                        <div style={{"clear":"both"}}></div>
                        </div>
                    </Panel>
                </View>
                <View id="notifications" activePanel="notifications">
                    <Panel id="notifications">
                        <PanelHeader></PanelHeader>
                        {this.getNotificationPage()}
                    </Panel>
                </View>
                <View id="user" activePanel="user">
                    <Panel id="user">
                        <PanelHeader></PanelHeader>
                        <AppHome id="home" go="persik" dataUsers={this.state.userSystemInfo} setStatusNotification={this.setStatusNotification.bind(this)}/>
                    </Panel>
                </View>
            </Epic>
        )
    }
}

export default App;